class Popper extends React.Component{
    constructor(){
        super();
        this.state = {name:'Hello world!'};
    }
    
    preventPop(name, e){    //‰∫ã‰ª∂ÂØπË±°eË¶ÅÊîæÂú®ÊúÄÂêé
        e.preventDefault();
        alert(name);
    }
    
    render(){
        return (
            <div>
                <p>hello</p>
                {/* ÈÄöËøá bind() ÊñπÊ≥ï‰º†ÈÄíÂèÇÊï∞„ÄÇ */}
                <a href="https://reactjs.org" onClick={this.preventPop.bind(this,this.state.name)}>Click</a>
               <br/>
                <a href="https://baidu.com" onClick={(e)=>this.preventPop(this.state.name,e)}>ÁôæÂ∫¶</a>

           
            </div>
        );
    }
}


